.titleBottom {
  margin-bottom: 35vh!important;
}
.yella {
  list-style: none;
}
.yellowBull::before {
  content: "\2022";
  color: #ffee58;
  font-weight: 900;
  display: inline-block;
  width: 1em;
  margin-left: -2.4em;
}
.techCont {
  text-align: left;
}
.techTitleWrap {
  display: inline-flex;
}
.techTitle {
  font-size: 2.7rem;
  color: rgb(97, 218, 251);
  text-decoration: underline;
}
.techList {
  font-size: 1.7rem;
  text-align: left;
}
.techIcons {
  line-height: 5;
}
.tools {
  margin-right: 8rem;
}
.titleLeftMargin {
  margin-left: 2rem;
}
@media only screen and (max-width: 1295px) {
  .techList {
    margin-left: 34%;
  }
  .titleLeftMargin {
    margin-left: 0rem;
  }
}
@media only screen and (max-width: 1201px) {
  .techList {
    margin-left: 40%;
  }
  .titleBottom {
    margin-bottom: 25vh!important;
  }
}
@media only screen and (max-width: 420px) {
  .techList {
    margin-left: 14%;
  }
}